@import '../../lib/sass/px2rem';
@import '../../component/the-promise/the-promise';
@import '../../component/baseBtn/baseBtn';

.top { background: #f6f6f6; padding: q(20) 0 0;
    .title { background: #fff; padding: q(10) q(38) q(11); display: flex; flex-direction: row; align-items: center; color: #666; border-top: 1px solid #e6e6e6; border-bottom: 1px solid #e6e6e6; margin-bottom: q(12);
        .deduction { width: q(284); margin-right: q(24); font-size: q(30); line-height: q(32);}
        .single { width: q(154);  margin-right: q(46); font-size: q(26); line-height: q(28);}
        .all { width: q(167); font-size: q(24); line-height: q(26);}
    }
    .content { margin: 0 auto; border: 1px solid #e6e6e6; width: q(714);
        .list { min-height: q(148); display: flex; flex-direction: row; background: #fdfdfd;  padding: 0 q(19) 0 q(20);
            &.gray { background: #f9f9f9;}
            div { display: flex; align-items: center; justify-content: center;}
            .list_l { width: q(284); margin-right: q(24); font-size: q(26); line-height: q(38);}
            .list_c { width: q(154); margin-right: q(46); font-size: q(28); line-height: q(36); color: #c1c0c0;}
            .list_r { width: q(167); font-size: q(28); line-height: q(36);}
        }
        .big {
            .list_l { font-size: q(38);}
        }
    }
}
.know_pro { padding: q(39) q(25) q(38) q(40); border-bottom: 1px solid #e6e6e6;}
.to_sure { padding: q(59) q(20) q(69);}
